<template>
  <el-container class="person-el-container">
    <el-header class="person-el-header">
      <el-page-header @back="goBack" content="个人信息"/>
    </el-header>
    <el-main class="person-el-main">
      <!-- id -->
      <tr>
        <td>ID:</td>
        <td>{{this.ccurrentPersonMsg.id}}</td>
        <td style="width:calc(40vw - 360px);"></td>
        <td></td>
      </tr>
      <el-divider/>
      <!-- 用户名 -->
      <tr>
        <td>用户名:</td>
        <td>{{this.ccurrentPersonMsg.username}}</td>
        <td style="width:calc(40vw - 360px);"></td>
        <td></td>
      </tr>
      <el-divider/>
      <!-- 性别 -->
      <tr>
        <td>性别:</td>
        <td>{{this.ccurrentPersonMsg.sex}}</td>
        <td style="width:calc(40vw - 360px);"></td>
        <td></td>
      </tr>
      <el-divider/>
      <!-- 方向 -->
      <tr>
        <td>方向:</td>
        <td>{{this.ccurrentPersonMsg.direction}}</td>
        <td style="width:calc(40vw - 360px);"></td>
        <td></td>
      </tr>
      <el-divider/>
      <!-- 权限 -->
      <tr>
        <td>权限:</td>
        <td>{{this.ccurrentPersonMsg.power | getPower}}</td>
        <td style="width:calc(40vw - 360px);"></td>
        <td></td>
      </tr>
      
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'MainPersonMsg',
  props: {
    ccurrentPersonMsg: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    }
  },
  filters: {
    getPower(val) {
      if(val === 'common') return '普通用户'
      if(val === 'admin') return '管理员'
    }
  }
}
</script>

<style>
  .person-el-main {
    margin: 0 32px;
  }
  .el-divider {
    background-color: rgba(125, 125, 125, 0.4);
  }
  .person-el-main td {
    line-height: 44px;
    width: 180px;
    text-align: center;
  }
  .el-button--primary {
    background-color: #79bbff;
    border-color: #79bbff;
  }
  .el-button--success {
    background-color: #79c255;
    border-color: #79c255;
  }
</style>